/*
* 队列管道
* */
class BlueQueuePipe {
    constructor(opts = {}) {
        this._init(opts);
    }
    _init(opts) {
        //配置
        this.options = opts;
        //队列
        this.queue = [];
        //数据
        this.data = opts.data || {};
    }
    enqueue(obj) {
        this.hook(this, this.options.enqueued, [this.queue.push(obj)]);
    }
    dequeue() {
        const { queue, options } = this;
        const dequeue = queue.shift();
        this.hook(this, options.dequeued, [dequeue]);
        return dequeue;
    }
    isEmpty() {
        return this.queue.length === 0;
    }
    clear() {
        this.queue = [];
    }
    first() {
        return this.queue[0];
    }
    //获取最后一个队列
    last() {
        return this.queue[this.queue.length - 1];
    }
    //执行队列
    run(...args) {
        const opts = this.options;
        while (!this.isEmpty()) {
            const dequeue = this.dequeue();
            //如果队列项是function，执行
            this.hook(this, opts.running, [(() => {
                    if (typeof dequeue === 'function') {
                        return dequeue.apply(this, args);
                    }
                    return dequeue;
                })()]);
        }
        this.hook(this, opts.ran);
    }
    //执行function
    hook(context, cb, args = []) {
        if (typeof cb === 'function') {
            return cb.apply(context, args);
        }
        return cb;
    }
    //使用方法
    useMethod(name, args) {
        const opts = this.options;
        if (!opts.methods)
            return;
        return this.hook(this, opts.methods[name], args || []);
    }
}
export default BlueQueuePipe;
